<template>
  <div class="container">
    <span v-if="user">こんにちは{{ user.email }}さん</span>
    <span v-else>ログインしていません</span>
  </div>
</template>

<script>
// サービスクラスをインポート
import { authService } from "@/services/AuthService";

export default {
  name: "home",
  data() {
    return {
      user: null
    };
  },
  //createdはライフサイクルフックと呼ばれるものの1つで、レンダリングとともに処理を実行できるのでユーザの体感速度を上げることができる。
  created() {
    // 直接呼び出していた部分をサービスクラス経由に変更
    authService.onStateChanged(user => {
      this.user = user;
    });
  }
};
</script>
