<template>
  <span class="icon">
    <!-- 属性およびコンポーネントプロパティの値を動的に設定します。 -->
    <i :class="['fas', nameClass]"></i>
  </span>
</template>

<script>
export default {
  name: "hm-icon",
  props: { name: String },
  //computed このプロパティに定義された関数はテンプレート内で読み取ることができます。基本的には props で渡されたデータに対して計算をした結果を返すことが多いです。
  computed: {
    //name の値を使用して nameClass は先頭に fa- をつけた値を返します。
    nameClass() {
      return `fa-${this.name}`;
    }
  }
};
</script>
